@import url('https://fonts.googleapis.com/css?family=Roboto:100,300,300i,400,400i,600');

@media screen and (max-width: 1024px) {
  .leftNav {
    text-align: center;
    height: 20px;
    width: 100%;
  }

  .rightLinks {
    display: none;
  }

  .searchMagGlassMinView {
    position: absolute;
    top: 5px;
    right: 6px;
    width: 50px;
  }

  .header-outer {
    position: absolute;
    /*padding-left: 20px;
    padding-right: 20px;*/
    top: 45px;
    width: 100%;
    height: 30px;
    background: black;
    text-align: center;

  }

  .whiteLogo {
    width: 189px;
  }

  .shoppingCart {
    position: absolute;
    width: 25px;
    right: 16px;
  }

  .miniMenu {
    position: absolute;
    cursor: pointer;
    left: 15px;
    font-size: 12px;
  }

  .lineAboveMenu {
    position: absolute;
    border-radius: 50px;
    left: 15px;
    width:30px;;
    height:3px;
    background-color: white;
  }

  .view {
    position: absolute;
    top: 90px;
  }

}

@media screen and (min-width: 1025px) {

  .leftNav {
    /*padding-left: 20px;*/
    text-align: center;
    height: 20px;
    width: 505.188px;
  }

  .rightLinks {
    position: absolute;
    right: 44.50px;
    /*margin-left: 20px;*/
  }

  .rightLinks a {
    width: 77.375;
    length: 20px;
    text-decoration: none;
    color: #d7d7d7;

    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 20px;
  }

  .rightLinks a:hover {
    text-decoration: underline;
  }

  ::-webkit-input-placeholder {
   color: black;
  }

  .searchInput {
    height: 41px;
    width: 190px;
    font-size: 12px;
    font-weight: 600;
    background-color: #ccc;
    border: 1px solid #c2c2c2;
    outline-color: initial;
    outline-style: initial;
    outline-width: 0px;
    outline-color: initial;
    outline-style: initial;
    padding-left: 9px;
    padding-right:35px;
  }

  .searchMagGlassMinView {
    display: none;
  }

  .header-outer {
    display: none;
  }

  .whiteLogo {
    display: none;
  }

  .miniMenu {
    display: none;
  }

  .lineAboveMenu {
    display: none;
  }

  .view {
    position: absolute;
    top: 45px;
  }

}
